import React from 'react';
import { Row, Col, Container, Section } from 'react-materialize';


function R(props) {
  if (props.imgfirst) {
    return (
      <Row >
        <Col className="hide-on-small-only" m={6} l={6} offset="l1">
          <h2 >{props.hdr}</h2>
          <p className="red-text flow-text">{props.txt}</p>
        </Col>
        <Col  s={12} m={5} l={4} pull="" >
          <img className="responsive-img" style={{marginTop: '120px'}}src={props.img} />
        </Col>
        <Col className="hide-on-med-and-up " s={12} offset="">
          <h3>{props.hdr}</h3>
          <p className="red-text flow-text">{props.txt}</p>
        </Col>
      </Row>
    );
  } else {
    return (

      <Row >
        <Col s={12} m={5} l={4} offset="l1">
          <img className="responsive-img" style={{marginTop: '120px'}}src={props.img} />
        </Col>
        <Col s={12} m={7} l={5} offset="l1">
          <h3>{props.hdr}</h3>
          <p className="grey-text text-darken-2 flow-text">{props.txt}</p>
        </Col>
      </Row>
    );
  }
}


